
:root {
    /* dark shades of primary color*/
    --clr-primary-1: hsl(205, 86%, 17%);
    --clr-primary-2: hsl(205, 77%, 27%);
    --clr-primary-3: hsl(205, 72%, 37%);
    --clr-primary-4: hsl(205, 63%, 48%);
    /* primary/main color */
    --clr-primary-5: hsl(205, 78%, 60%);
    /* lighter shades of primary color */
    --clr-primary-6: hsl(205, 89%, 70%);
    --clr-primary-7: hsl(205, 90%, 76%);
    --clr-primary-8: hsl(205, 86%, 81%);
    --clr-primary-9: hsl(205, 90%, 88%);
    --clr-primary-10: hsl(205, 100%, 96%);
    /* darkest grey - used for headings */
    --clr-grey-1: hsl(209, 61%, 16%);
    --clr-grey-2: hsl(211, 39%, 23%);
    --clr-grey-3: hsl(209, 34%, 30%);
    --clr-grey-4: hsl(209, 28%, 39%);
    /* grey used for paragraphs */
    --clr-grey-5: hsl(210, 22%, 49%);
    --clr-grey-6: hsl(209, 23%, 60%);
    --clr-grey-7: hsl(211, 27%, 70%);
    --clr-grey-8: hsl(210, 31%, 80%);
    --clr-grey-9: hsl(212, 33%, 89%);
    --clr-grey-10: hsl(210, 36%, 96%);
    --clr-white: #fff;
    --clr-red-dark: hsl(360, 67%, 44%);
    --clr-red-light: hsl(360, 71%, 66%);
    --clr-green-dark: hsl(125, 67%, 44%);
    --clr-green-light: hsl(125, 71%, 66%);
    --clr-black: #222;
    --transition: all 0.3s linear;
    --spacing: 0.1rem;
    --radius: 0.25rem;
    --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    --max-width: 1170px;
    --fixed-width: 620px;
  }

* {
    margin: 0;
    padding: 0;
}

.main {
    background: var(--clr-grey-9);
    
}
.logo-container{
    display: flex;
    align-items: center;
    font-family: 'Titillium Web', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-shadow: 2px 2px 2px var(--clr-grey-4);
}

.header{
    background: rgb(255,255,255);
    background: linear-gradient(48deg, rgba(255,255,255,1) 0%, rgba(0,128,128,1) 100%);



}
.header img{
    width: 80px;
    margin: 20px 0px 10px 20px;
}
.category {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    margin-top: 20px;
    margin-bottom: 20px;
   
}


.category-btn,
.search-btn{
    color: var(--clr-primary-1);
    background: var(--clr-grey-10);
    border: 1px solid var(--clr-primary-1);
    font-family: 'Titillium Web', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border-radius: var(--radius);
   margin: 5px;
   height: 40px;
}
.search-btn{
    width: 100px;
    height: 30px;
}

.search-form{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-family: 'Titillium Web', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-bottom: 10px;
    padding: 20px 10px 20px 10px;
}

.search-form input{
    width: 40%;

    height: 30px;
    font-size: 20px;
    font-family: 'Titillium Web', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   
}

.grid-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    
    grid-gap: 20px 20px;
    margin: 0px 20px 0px 20px;

}

@media screen and (max-width: 980px){
    .grid-container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        
       
    
    }
}

@media screen and (max-width: 660px){
    .grid-container{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        
       
    
    }
    .category {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        margin-top: 20px;
        margin-bottom: 20px;
        
    }
}